import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


function Square(props) {
  return (
    <button className={props.color} onClick={props.onClick} >
      {props.value}
    </button >
  );
}

class Board extends React.Component {

  renderSquare(i) {
    return (
      <Square
        value={this.props.squares[i]}
        color={this.props.colors[i]}
        onClick={() => this.props.onClick(i)}
      />);
  }

  render() {
    let rowCount = 3, columnCount = 3;
    let newBoard = []
    while (rowCount--) {
      columnCount = 3
      let colSquares = []
      while (columnCount--) {
        colSquares.push(this.renderSquare((rowCount * 3) + columnCount));
      }
      let oneRow = <div className="board-row">{colSquares}</div>
      newBoard.push(oneRow)
    }

    return (
      <div>{newBoard}</div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [{
        squares: Array(9).fill(null),
      }],
      stepNumber: 0,
      XIsNext: true,
    }
  }
  handleClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }

    squares[i] = this.state.XIsNext ? 'X' : 'O';
    this.setState({
      history: history.concat([{
        squares: squares,
      }]),
      stepNumber: history.length,
      XIsNext: !this.state.XIsNext,
    });
  }

  jumpTo(step) {
    this.setState({
      stepNumber: step,
      XIsNext: (step % 2) === 0,
    });
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winData = calculateWinner(current.squares);

    const moves = history.map((step, move) => {
      const desc = move ?
        'Go To move #' + move :
        'Go to Starting Of Game';
      return (
        <li key={move}>
          <button onClick={() => this.jumpTo(move)}>{desc}</button>
        </li>
      );
    })
    let status;
    let coloredArray = Array(9).fill("square");
    if (winData != null && winData.winner) {
      status = 'Winner: ' + winData.winner;
      console.log(winData.winningSquares);
      winData.winningSquares.forEach((wonIndex) => { coloredArray[wonIndex] = "greenBox" });
      //this.setState({ colors: coloredArray });
    } else {
      status = 'Next player: ' + (this.state.XIsNext ? 'X' : 'O');
    }
    return (
      <div className="game">
        <div className="game-board">
          <Board
            squares={current.squares}
            colors={coloredArray}
            onClick={(i) => this.handleClick(i)} />
        </div>
        <div className="game-info">
          <div>{status}</div>
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return { winner: squares[a], winningSquares: [a, b, c] };
    }
  }
  return null;
}